.displaytitle{
    font-family: Arial, Helvetica, sans-serif;
    padding: 16px;
    font-size: 48px;
  text-transform: uppercase;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}

.room-available{
    background-color: #34a940;
    font-family: Arial, Helvetica, sans-serif;
    padding: 16px;
    
    text-align: left !important;
    font-size: 18px !important;
color:#ffffff;
  overflow: hidden;
  border-radius: 10px;
}

.room-available button {
    background-color: transparent;
    color:#ffffff;
    border: 1px solid white;
    padding: 8px;
}
.room-buzy{
    background-color: #9d2524;
}

.room-soonbuzy{
    background-color: #34a940;
}
.status-block{
    padding-top: 12px;
    padding-bottom: 32px;
}
.status-heading{
    font-size: 32px;
    line-height: 32px;
  
}

.status-subheading{
    
    padding-top: 4px;
    font-size: 18px;
}